import React from 'react'
import { Button, StyleSheet, Text, TouchableOpacity, View } from 'react-native';
import $ from 'jquery'

var from="K"

function Temp(){
    return(
        <div>
            <p id="fromTitle" color="#9000a0"></p>
            <input type="number" placeholder='temperatura' id="fromTemp" step="any"></input>
            <br></br>
            <Button title="from K" onPress={ofK} color="#9000a0"></Button>
            <Button title="from F" onPress={ofF} color="#9000a0"></Button>
            <Button title="from C" onPress={ofC} color="#9000a0"></Button>
            <br></br>
            <div>
            <Button title="to K" onPress={toK} color="#9000a0"></Button>
            <Button title="to F" onPress={toF} color="#9000a0"></Button>
            <Button title="to C" onPress={toC} color="#9000a0"></Button>
            </div>
            <br></br>
            <p id="toTemp" color="#9000a0"></p>
        </div>
    )
}
function toK(){
    var temp=$("#fromTemp").val()
    temp=parseInt(temp)
    var K=toKfrom(temp)
    var res=K
    console.log(res)
    $("#toTemp").text(res+" K")
}
function toF(){
    var temp=$("#fromTemp").val()
    temp=parseInt(temp)
    var K=toKfrom(temp)
    var res=((K - 273.15) * 9/5) + 32

    console.log(res)
    $("#toTemp").text(res+" F")
}
function toC(){
    var temp=$("#fromTemp").val()
    temp=parseInt(temp)
    var K=toKfrom(temp) 
    var res=K - 273.15
    console.log(res)
    $("#toTemp").text(res+" C")
}
function toKfrom(temp,fromScale){
    var res=0
    var fromScale=getRadio()
    console.log(" f"+fromScale)
    if(fromScale=="K"){
        res=temp
    }else if(fromScale=="F"){
        res=((temp - 32) * 5/9) + 273.15
    }else if(fromScale=="C"){
        res=temp+273.15
    }
    return res
}
function getRadio(){
    
    return from
}
function ofK(){
    from="K"
    $("#fromTitle").text("from "+from+"")
}
function ofF(){
    from="F"
    $("#fromTitle").text("from "+from+"")
}
function ofC(){
    from="C"
    $("#fromTitle").text("from "+from+"")
}

export default Temp;

